<template>
    <div class='search'>
        <div class='search_header'>
            <img src="../../assets/image/search_big.png" alt="" class='search_icon'>
            <div class='input'>
                <!-- <form action="javascript:void 0"> -->
                <input autofocus='autofocus' maxlength="2000" type="search" v-model="content" @keyup.13="tapToSearch"   class='inputContent'>
                <!-- </form> -->
                <!-- <input type="text" class='input' v-model="content"> -->
                <div class='delete' @click='deleteWord()'>
                    <img src="../../assets/image/删除.png" alt="">
                </div>
            </div>
            <div class='cancel' @click='cancel()'>取消</div>
        </div>
        <div class="page-content" v-show="content !== '' && content != null && wordStudyData.length !== 0 ">
           <mt-loadmore ref="loadmore" class="loadmore"
                :top-method="loadTop"  infinite-scroll-distance="40" infinite-scroll-immediate-check="false" :autoFill="false" :maxDistance='150' :bottom-all-loaded="allLoaded"
                :bottom-method="loadBottom">
                <div :class="wordStudyData.length >= pageSize?'wordStudy_item_heard ': 'wordStudy_item_heard_two'">
                <div  v-for='(item ,index) in wordStudyData' :key="index" class='wordStudy_item' >

                  <div class='wordStudy' v-if='item.type =="1201" || !item.type' @click.stop='enterStudy(item.id,item.name,item.specialCode,item.url,item.fileType)'>
                      <div class='wordStudycontainLeft'>
                          <div class='wordStudyName'>{{item.name}}</div>
                          <div class='wordStudyother'>
                              <div class='wordStudybrowse'>
                                  浏览 {{item.viewNum}}
                              </div>
                          </div>
                      </div>
                  </div>

                  <div class='videoStudy' v-if='item.type =="1202"' @click.stop='enterVideoDetail(item.id,item.name,item.specialCode)'>
                      <div class='videoStudycontainLeft'>
                          <div class='videoStudyName'>{{item.name}}</div>
                          <div class='videoStudybrowse'>浏览 {{item.viewNum}}</div>
                      </div>
                      <div class='videoStudycontainRight'>
                          <img src="../../assets/image/videoImg.png" alt="" class='poster' v-if='!item.imageUrl'>
                          <img :src="item.imageUrl" alt="" class='poster' v-if='item.imageUrl'>
                          <div class='videoPlay'>
                              <img src="../../assets/image/播放.png" alt="" class='playButton'>
                          </div>
                      </div>
                  </div>


                  <div class='newsList' v-if='item.type =="0"' @click.stop='enterNewsDetail(item.id,item.title)'>
                      <div class='newsListLeft'>
                        <div class='newsListTop'>
                            {{item.title}}
                        </div>
                        <div class='newsListBottom'>
                            <div class='newsListBottomSource'>
                                {{item.source}}
                            </div>
                            <div style='margin-left:0.25rem;padding-bottom:0.02rem'>
                                {{item.publishTime}}
                            </div>
                        </div>
                    </div>
                    <div class='newsListRight'>
                        <img src="../../assets/image/新闻图.png" alt="" v-if='!item.newsImg'>
                        <img :src="item.newsImg" alt="" v-if='item.newsImg'>
                    </div>
                  </div>
                </div>
                </div>
                <div class="more_loading" style="height:0.30rem; line-height:0.30rem; text-align:center;color:#ccc" v-show="queryLoading01 && wordStudyData.length >= 10">
                        ---- 看到底线了 ----
                </div>
           </mt-loadmore>
        </div>
        <div class='noData'>
            <img src="../../assets/image/noData.png" alt="">
            <div class='noDataWarning'>暂时没有找到相关信息</div>
        </div>
        <div class='history'>
            <div class='history_top'>历史搜索</div>
            <div class='history_container'>
                <div class='history_item' v-for="item in historyContainer" :key="item.name" @click='setSearch(item)'>
                    <pre>{{item}}</pre>
                </div>
            </div>
            <div class='qingkong'>
                <div class='qingkongbutton' @click='qingkong()'>
                    <img src="../../assets/image/清空.png" alt="">
                    <div>清空</div>
                </div>
            </div>
        </div> 
    </div>
</template>
<script>
import API from '@/api/api'
import $ from 'jquery'
import { debuglog } from 'util';
import { scrypt } from 'crypto';
import { Script } from 'vm';
import store from '@/store' // 引入vuex
export default {
  name: 'search',
  data () {
    return {
      getDataSuccess:false,
      pageNum: 1 , // 当前页号
      pageSize: 10 , // 一页大小
      allLoaded: false, // 列表数据是否全部加载
      wordStudyData: [] , // 数据集合
      content: '张震', // 输入框的数据
      queryLoading01: false , // 是否加载全部
      content: '',
      enterIndex: '',
      historyContainer: [],
      personId: '', // 用户id
      allhistory: [],
      specialCodeList: [], // 专项列表
      topStatus: false, // 顶部下拉刷新
      pageCount: 100,
    }
  },
  mounted () {
    // 监听返回键
    document.addEventListener('backbutton', this.onBackKeyDown, false)
  },
  created () {
    this.getClassifyList()
    this.isBlur()
    this.changfouce()
    this.specialCode = localStorage.getItem('dataListSpecialCode')
    this.enterIndex = localStorage.getItem('enterIndex')
    if (localStorage.getItem('content')) {
      this.content = localStorage.getItem('content')
    } else {
      this.content = ''
    }
  },
  methods: {
    // 下拉刷新
    loadTop () {
      this.pageNum = 1
      this.allLoaded = false
      setTimeout(() => {
        this.selectMateAndSch('down')
      }, 500)
    },
    // 上拉加载
    loadBottom () {
      this.queryLoading01 = false;
      setTimeout(() => {
        this.pageNum += 1
        this.selectMateAndSch('up')
      }, 500)
    },
    /**
     * 获取数据信息
     */
    selectMateAndSch (flag) {
      if (this.enterIndex === this.GLOBAL.ENTERSOURCE.newsSearch) { // 获取新闻接口
        this.getNewsList(flag);
      }else if (this.enterIndex === this.GLOBAL.ENTERSOURCE.bookSearch) { // 获取文档资料接口
        this.getBookSearch(flag);
      }else if (this.enterIndex === this.GLOBAL.ENTERSOURCE.videoSearch) { // 获取视听学习接口
        this.getVideoSearch(flag);
      }else if (this.enterIndex === this.GLOBAL.ENTERSOURCE.studySearch) { // 获取所有资料接口
        this.getStudySearch(flag);
      }
    },
    /**
     * 获取新闻集合的列表
     */
    getNewsList(flag){
      API.post('findNewsBase', {
          pageMsg: {
            currentPage:this.pageNum,
            pageSize:this.pageSize
          },
          queryFilter:{
            title :this.content,
            status: '1302'
          }
      }, 'json', (data, err) => {
        if (data.success) {
          this.getDataSuccess = true
          if (flag === 'down') {
            this.wordStudyData = [];
            this.queryLoading01 = false;
            this.$refs.loadmore.onTopLoaded()
          }else {
            this.$refs.loadmore.onBottomLoaded();
          }
          for (var i = 0; i < data.data.records.length; i++) {
            data.data.records[i].publishTime = this.GLOBAL.changeTime(data.data.records[i].publishTime)
          }
          for (let i = 0; i < data.data.records.length; i++) {
            this.wordStudyData.push(data.data.records[i])
          }
          if (this.wordStudyData.length === data.data.total) {
            this.queryLoading01 = true;
          }else{
            this.queryLoading01 = false;
          }
        } else {
          console.log(data.msg)
        }
      })
    },
    /**
     * 获取文档资料接口
     */
    getBookSearch(flag){
      API.post('allfindPage', {
          specialCode: this.specialCode,
          keyword: this.content,
          pageNum: this.pageNum - 1,
          pageSize: this.pageSize
      }, 'json', (data, err) => {
        if (data.success) {
          this.getDataSuccess = true
          if (flag === 'down') {
            this.wordStudyData = [];
            this.queryLoading01 = false;
            this.$refs.loadmore.onTopLoaded()
          }else {
            this.$refs.loadmore.onBottomLoaded();
          }
          for (var i = 0; i < data.data.records.length; i++) {
            data.data.records[i].publishTime = this.GLOBAL.changeTime(data.data.records[i].publishTime)
          }
          for (let i = 0; i < data.data.records.length; i++) {
            this.wordStudyData.push(data.data.records[i])
          }
          if (this.wordStudyData.length === data.data.total) {
            console.log(111111);
            
            this.queryLoading01 = true;
          }else{
            console.log(2222);
            this.queryLoading01 = false;
          }
        } else {
          console.log(data.msg)
        }
      })
    },
    /**
     *  获取视听学习接口
     */
    getVideoSearch(flag){
      API.post('selectMateAndSche', {
          name: this.content,
          specialCode: this.specialCode,
          type: '1202',
          pageNum: this.pageNum,
          pageSize: this.pageSize
        }, 'json', (data, err) => {
          if (data.success) {
            this.getDataSuccess = true
            if (flag === 'down') {
              this.wordStudyData = [];
              this.queryLoading01 = false;
              this.$refs.loadmore.onTopLoaded()
            }else {
              this.$refs.loadmore.onBottomLoaded();
            }
            for (var i = 0; i < data.data.records.length; i++) {
              data.data.records[i].publishTime = this.GLOBAL.changeTime(data.data.records[i].publishTime)
            }
            for (let i = 0; i < data.data.records.length; i++) {
              this.wordStudyData.push(data.data.records[i])
            }
            if (this.wordStudyData.length === data.data.total) {
              this.queryLoading01 = true;
            }else{
              this.queryLoading01 = false;
            }
          } else {
            console.log(data.msg)
          }
      })
    },
    /**
     * 获取所有资料接口
     */
    getStudySearch(flag){
      API.post('allfindPage', {
          // name: this.content
          // specialCode:this.specialCode
          keyword:this.content,
          pageNum:this.pageNum - 1,
          pageSize:this.pageSize
        }, 'json', (data, err) => {
          if (data.success) {
            this.getDataSuccess = true
            if (flag === 'down') {
              this.wordStudyData = [];
              this.queryLoading01 = false;
              this.$refs.loadmore.onTopLoaded()
            }else {
              this.$refs.loadmore.onBottomLoaded();
            }
            for (var i = 0; i < data.data.records.length; i++) {
              data.data.records[i].publishTime = this.GLOBAL.changeTime(data.data.records[i].publishTime)
            }
            for (let i = 0; i < data.data.records.length; i++) {
              this.wordStudyData.push(data.data.records[i])
            }
            if (this.wordStudyData.length === data.data.total) {
              this.queryLoading01 = true;
            }else{
              this.queryLoading01 = false;
            }
          } else {
            console.log(data.msg)
          }
      })
    },
    // 获取个人信息
    getFindWeekTime () {
      this.getFindWeekTimeCG = false
      API.post('findWeekTime', {}, 'json', (data, err) => {
        if (data.success) {
          this.personId = data.data.one.person.id
          this.olddata()
        } else {
          this.$toast(data.msg)
        }
      })
    },
    olddata () {
      let obj = {}
      obj.personId = this.personId
      if (this.enterIndex === this.GLOBAL.ENTERSOURCE.bookSearch) {
        this.olddataBookSearch(obj);
      } else if (this.enterIndex === this.GLOBAL.ENTERSOURCE.videoSearch) {
        this.olddataVideoSearch(obj);
      } else if (this.enterIndex === this.GLOBAL.ENTERSOURCE.studySearch) {
        this.olddataStudySearch(obj);
      } else if (this.enterIndex === this.GLOBAL.ENTERSOURCE.newsSearch) {
        this.olddataNewsSearch(obj);
      }
    },
    olddataBookSearch(obj){
      for (var y = 0; y < this.specialCodeList.length; y++) {
        if (this.specialCode === this.specialCodeList[y].id) {
          if (!localStorage.getItem(this.specialCode + 'bookHistory')) {
            this.historyContainer = []
            obj.historyContainer = []
            this.allhistory.push(obj)
          } else {
            this.allhistory = JSON.parse(localStorage.getItem(this.specialCode + 'bookHistory'))
            for (var i = 0; i < this.allhistory.length; i++) {
              if (this.personId === this.allhistory[i].personId) {
                this.historyContainer = this.allhistory[i].historyContainer
                return
              }
            }
            obj.historyContainer = []
            this.allhistory.push(obj)
          }
        }
      }
    },
    olddataVideoSearch(obj){
      for (var y2 = 0; y2 < this.specialCodeList.length; y2++) {
        if (this.specialCode === this.specialCodeList[y2].id) {
          if (!localStorage.getItem(this.specialCode + 'videoHistory')) {
            this.historyContainer = []
            obj.historyContainer = []
            this.allhistory.push(obj)
          } else {
            this.allhistory = JSON.parse(localStorage.getItem(this.specialCode + 'videoHistory'))
            for (var i2 = 0; i2 < this.allhistory.length; i2++) {
              if (this.personId === this.allhistory[i2].personId) {
                this.historyContainer = this.allhistory[i2].historyContainer
                return
              }
            }
            obj.historyContainer = []
            this.allhistory.push(obj)
          }
        }
      }
    },
    olddataStudySearch(obj){
      if (!localStorage.getItem('dataListHistory')) {
        this.historyContainer = []
        obj.historyContainer = []
        this.allhistory.push(obj)
      } else {
        this.allhistory = JSON.parse(localStorage.getItem('dataListHistory'))
        for (var i3 = 0; i3 < this.allhistory.length; i3++) {
          if (this.personId === this.allhistory[i3].personId) {
            this.historyContainer = this.allhistory[i3].historyContainer
            return
          }
        }
        obj.historyContainer = []
        this.allhistory.push(obj)
      }
    },
    olddataNewsSearch(obj){
      if (!localStorage.getItem('newsListHistory')) {
        this.historyContainer = []
        obj.historyContainer = []
        this.allhistory.push(obj)
      } else {
        this.allhistory = JSON.parse(localStorage.getItem('newsListHistory'))
        for (var i4 = 0; i4 < this.allhistory.length; i4++) {
          if (this.personId === this.allhistory[i4].personId) {
            this.historyContainer = this.allhistory[i4].historyContainer
            return
          }
        }
        obj.historyContainer = []
        this.allhistory.push(obj)
      }
    },
    // 获取专题分类的接口
    getClassifyList () {
      API.post('querySpecialType', {
        // dicId: this.GLOBAL.SPECIALCODE.dicId
        pid: ''
      }, 'json', (data, err) => {
        if (data.success) {
          this.specialCodeList = data.data
          this.getFindWeekTime()
        } else {
          this.$toast(data.msg)
        }
      })
    },
    // 获取失去焦点
    isBlur () {
      $('.inputContent').focus(() => {
        if (!this.content) {
          $('.delete').css('display', 'none')
        } else {
          $('.delete').css('display', 'flex')
        }
      })
      $('.inputContent').blur(() => {
        setTimeout(() => {
          $('.delete').css('display', 'none')
        }, 1)
      })
    },
    // 自动聚焦
    changfouce () {
      this.$nextTick(() => {
        $('.inputContent').focus()
      })
    },
    // 取消按钮
    cancel () {
      this.$router.back()
      localStorage.removeItem('content')
    },
    // 物理返回键
    onBackKeyDown () {
      this.cancel()
    },
    // 删除图标
    deleteWord () {
      this.content = ''
    },
    // 点击软键盘搜索
    tapToSearch () {
      if (this.historyContainer.length >= 1) {
        for (var i = 0; i < this.historyContainer.length; i++) {
          if (this.historyContainer[i] === this.GLOBAL.filterKG(this.content)) {
            this.historyContainer.splice(i, 1)
          }
        }
      }
      var parten = /^\s*$/
      // 执行验证空格
      if (parten.test(this.content)) {

      } else {
        this.historyContainer.unshift(this.GLOBAL.filterKG(this.content))
      }
      if(!this.getDataSuccess){
        return
      }
      $('.inputContent').blur()
      if (this.wordStudyData.length === 0) {
        $('.history').css('display', 'none')
        $('.wordStudy_container').css('display', 'none')
        $('.noData').css('display', 'flex')
      } else {
        $('.history').css('display', 'none')
        $('.wordStudy_container').css('display', 'block')
        $('.noData').css('display', 'none')
      }     
    },
    // 清空
    qingkong () {
      this.historyContainer = []
    },
    // 进入阅读
    enterStudy (id, name, specialCode,url,fileType) {
      API.post('selectLastContent', {
        trainMateId: id
      }, 'json', (data, err) => {
        if (data.success) {
          if (fileType === 'epub') {
            this.$router.push({
              name: 'text_train',
              params: { id: id, name: name, specialCode: specialCode }
            })
          } else {
            var u = navigator.userAgent // 判断是安卓还是ios
            var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1 // g
            var isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/) // ios终端
            if (isAndroid) {
              cordova.InAppBrowser.open(url, '_system', 'footer=yes,closebuttoncaption="返回",hideurlbar=yes,hidenavigationbuttons=yes,location=no')
            }
            if (isIOS) {
              cordova.InAppBrowser.open(url, '_system', 'footer=yes,closebuttoncaption="返回",hideurlbar=yes,hidenavigationbuttons=yes,location=no')
            }
          }
          for (var j = 0; j < this.historyContainer.length; j++) {
            if (this.historyContainer[j] === name) {
              this.historyContainer.splice(j, 1)
            }
          }
          this.historyContainer.unshift(name)
          for (var i = 0; i < this.allhistory.length; i++) {
            if (this.allhistory[i].personId === this.personId) {
              this.allhistory[i].historyContainer = this.historyContainer
            }
          }
          if (this.enterIndex === this.GLOBAL.ENTERSOURCE.bookSearch) {
            // localStorage.setItem('bookListSearch', this.content)
            for (var y = 0; y < this.specialCodeList.length; y++) {
              if (this.specialCode === this.specialCodeList[y].id) {
                localStorage.setItem(this.specialCode + 'bookHistory', JSON.stringify(this.allhistory))
              }
            }
          } else if (this.enterIndex === this.GLOBAL.ENTERSOURCE.studySearch) {
            // localStorage.setItem('dataListSearch', this.content)
            localStorage.setItem('dataListHistory', JSON.stringify(this.allhistory))
          }
        } else {
          this.$toast('该文档不存在')
          this.selectMateAndSch()
        }
      })
    },
    // 进入视频详情
    enterVideoDetail (id, name, specialCode) {
      API.post('findVideoDetilsById', {
        deflag: '0',
        id: id
      }, 'json', (data, err) => {
        if (data.success) {
          if (data.data) {
            this.$router.push({
              name: 'video_detail',
              params: { id: id, name: name, specialCode: specialCode }
            })
            for (var j = 0; j < this.historyContainer.length; j++) {
              if (this.historyContainer[j] === name) {
                this.historyContainer.splice(j, 1)
              }
            }
            this.historyContainer.unshift(name)
            for (var i = 0; i < this.allhistory.length; i++) {
              if (this.allhistory[i].personId === this.personId) {
                this.allhistory[i].historyContainer = this.historyContainer
              }
            }
            // console.log(this.historyContainer)
            if (this.enterIndex === this.GLOBAL.ENTERSOURCE.videoSearch) {
              // localStorage.setItem('VideoSearch', this.content)
              for (var y = 0; y < this.specialCodeList.length; y++) {
                if (this.specialCode === this.specialCodeList[y].id) {
                  localStorage.setItem(this.specialCode + 'videoHistory', JSON.stringify(this.allhistory))
                }
              }
            } else if (this.enterIndex === this.GLOBAL.ENTERSOURCE.studySearch) {
              // localStorage.setItem('dataListSearch', this.content)
              localStorage.setItem('dataListHistory', JSON.stringify(this.allhistory))
            }
          } else {
            this.$toast('该视频不存在')
            this.selectMateAndSch()
          }
        }
      })
    },
    // 进入新闻详情
    enterNewsDetail (id, name) {
      API.post('findNewsDetilById', {
        id: id,
        status: '1302'
      }, 'json', (data, err) => {
        if (data.success) {
          if (data.data) {
            this.$router.push({
              name: 'news_detail',
              params: { id: id, name: name }
            })
            for (var j = 0; j < this.historyContainer.length; j++) {
              if (this.historyContainer[j] === name) {
                this.historyContainer.splice(j, 1)
              }
            }
            this.historyContainer.unshift(name)
            for (var i = 0; i < this.allhistory.length; i++) {
              if (this.allhistory[i].personId === this.personId) {
                this.allhistory[i].historyContainer = this.historyContainer
              }
            }
            if (this.enterIndex === this.GLOBAL.ENTERSOURCE.newsSearch) {
              // localStorage.setItem('newsListSearch', this.content)
              localStorage.setItem('newsListHistory', JSON.stringify(this.allhistory))
            }
          } else {
            this.$toast('该新闻不存在')
            this.selectMateAndSch()
          }
        }
      })
    },
     // 点击历史记录，内容进入输入框
    setSearch (name) {
      this.content = name
      $('.inputContent').focus()
    }
  },
  // 消除返回键监听
  beforeDestroy () {
    document.removeEventListener('backbutton', this.onBackKeyDown, false)
  },
  watch: {
    queryLoading01(val, oldval){
      if (this.wordStudyData.length >= 10 && this.queryLoading01) {
        $('.mint-loadmore-bottom').css('display','none');
      }else {
        $('.mint-loadmore-bottom').css('display','flex');
      }
    },
    wordStudyData(){
      if (this.wordStudyData.length < 10) {
        this.allLoaded = true;
      }else {
        this.allLoaded = false;
      }
      if (this.wordStudyData.length >= 10 && this.queryLoading01) {
        $('.mint-loadmore-bottom').css('display','none');
      }else {
        $('.mint-loadmore-bottom').css('display','flex');
      }
    },
    content (val, oldval) {
      store.commit('clearToken') // 取消请求
      this.getDataSuccess = false
      if (val !== '') {
        $('.delete').css('display', 'flex')
        $('.history').css('display', 'none')
        $('.wordStudy_container').css('display', 'block')
        $('.noData').css('display', 'none')
      } else {
        $('.delete').css('display', 'none')
        $('.history').css('display', 'block')
        $('.wordStudy_container').css('display', 'none')
        $('.noData').css('display', 'none')
      }
      var parten = /^\s*$/
      // 执行验证
      if (parten.test(val)) {
        // console.log("all spaces!");
        this.wordStudyData = []
        return
      } else {
        // console.log("not all spaces");
        this.selectMateAndSch('down')
      }
      localStorage.setItem('content', val)
    },
    historyContainer (val, oldval) {
      if (val.length === 0) {
        $('.qingkong').css('display', 'none')
      } else {
        $('.qingkong').css('display', 'flex')
      }
      if (val.length >= 6) {
        val.splice(5, 1)
      }
      for (var i = 0; i < this.allhistory.length; i++) {
        if (this.allhistory[i].personId === this.personId) {
          this.allhistory[i].historyContainer = this.historyContainer
        }
      }
      if (this.enterIndex === this.GLOBAL.ENTERSOURCE.bookSearch) {
        for (var y = 0; y < this.specialCodeList.length; y++) {
          if (this.specialCode === this.specialCodeList[y].id) {
            localStorage.setItem(this.specialCode + 'bookHistory', JSON.stringify(this.allhistory))
          }
        }
      } else if (this.enterIndex === this.GLOBAL.ENTERSOURCE.videoSearch) {
        for (var j = 0; j < this.specialCodeList.length; j++) {
          if (this.specialCode === this.specialCodeList[j].id) {
            localStorage.setItem(this.specialCode + 'videoHistory', JSON.stringify(this.allhistory))
          }
        }
      } else if (this.enterIndex === this.GLOBAL.ENTERSOURCE.studySearch) {
        localStorage.setItem('dataListHistory', JSON.stringify(this.allhistory))
      } else if (this.enterIndex === this.GLOBAL.ENTERSOURCE.newsSearch) {
        localStorage.setItem('newsListHistory', JSON.stringify(this.allhistory))
      }
    }
  }
}
</script>
